/* View Product Styles */

/* Container for the product list */
.product-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 20px;
    justify-content: center;
  }
  
  /* Individual product card style */
  .product-card {
   
    background-color: #b6a7a7;
    border-radius: 8px;
    padding: 20px;
    width: 250px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .product-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  }
  
  /* Product image styling */
  .product-card img {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 8px;
  }
  
  /* Product name styling */
  .product-card h3 {
    font-size: 18px;
    font-weight: 600;
    margin-top: 10px;
    color: #333;
  }
  
  /* Short product description */
  .product-card p {
    font-size: 14px;
    color: #666;
    margin: 5px 0;
    line-height: 1.5;
  }
  
  /* Styling for MRP and Selling Price */
  .product-card p strong {
    font-weight: 600;
  }
  
  /* Styling for material, color, and dimensions */
  .product-card p:last-child {
    font-size: 12px;
    color: #888;
    margin-top: 10px;
  }
  
  /* Styling for the subsidiary images */
  .sub-images {
    display: flex;
    gap: 10px;
    margin-top: 10px;
  }
  
  .sub-images img {
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 5px;
    transition: transform 0.2s ease;
  }
  
  .sub-images img:hover {
    transform: scale(1.1);
  }
  
  /* Center the "No products available" message */
  .product-list p {
    text-align: center;
    color: #070000;
    font-size: 16px;
  }
  
  /* Add some padding around the View Product section */
  .product-section {
    padding: 40px;
  }
  