.sidebar {
    width: 240px;
    background: linear-gradient(180deg, #cdc0c0, #97b7d8);
    padding: 24px;
    height: 100vh;
    box-shadow: 2px 0 12px rgba(0, 0, 0, 0.08);
    display: flex;
    flex-direction: column;
    font-family: "Segoe UI", sans-serif;
  }
  
  .sidebar h2 {
    font-size: 26px;
    font-weight: 600;
    color: #3d566f;
    margin-bottom: 28px;
    letter-spacing: 0.5px;
  }
  
  .sidebar-section {
    margin-bottom: 30px;
  }
  
  .sidebar-section h4 {
    margin-bottom: 12px;
    font-size: 14px;
    color: #a462eb;
    text-transform: uppercase;
    font-weight: 500;
  }
  
  .sidebar-section ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .sidebar-section li {
    margin-bottom: 10px;
  }
  
  .sidebar-section a {
    display: block;
    text-decoration: none;
    color: #000000;
    padding: 10px 16px;
    border-radius: 8px;
    transition: all 0.3s ease;
    font-size: 15px;
    font-weight: 500;
  }
  
  .sidebar-section a:hover {
    background-color: #e0f7fa;
    color: #007b83;
    box-shadow: inset 4px 0 0 #007b83;
  }
  
  .sidebar-section a.active {
    background-color: #2f3b39;
    color: #148f77;
    box-shadow: inset 4px 0 0 #148f77;
  }
  